@import url(https://fonts.googleapis.com/css2?family=Iceland&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
body {
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: 'Roboto Condensed', sans-serif;
  color: white;
}

/* MultiPage Styling */

a {
  text-decoration: none;
}

.background-image {
  height: 100vh;
  background-image: url(/static/media/stadium.d32682d2.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: fixed;
  object-fit: cover;
  overflow-Y: scroll;
}

.btn {
  outline: none;
  border: none;
  border-radius: 6px;
  background-color: rgb(59, 121, 59);
  box-shadow: 4px 4px 3px 0px rgba(0,0,0,0.9);
  padding: 0.5rem 1.1rem;
  font-size: 1.2rem;
  letter-spacing: 0.1rem;
  font-family: 'Roboto Condensed', sans-serif;
  cursor: pointer;
  color: white;
}

.btn-nav {
  background-color: rgb(214, 145, 18);
}

.btn-red {
  background-color: rgb(197, 52, 52);
}

.btn-blue {
  background-color: rgb(54, 97, 153);
}

.btn-center {
  margin: 0 auto;
}

.btn-footer {
  display: block;
  margin: 0 auto 2rem;
}

.label {
  font-size: 1.3rem;
  margin-right: 0.9rem;
}

.rating-range {
  width: 12rem;
  margin-right: 1.1rem;
}

.input {
  background-color: white;
  outline: none;
  border-radius: 6px;
  height: 2rem;
  margin-bottom: 0.6rem;
  font-family: 'Roboto Condensed', sans-serif;
  text-align: center;
  font-size: 1.2rem;
}

.footer-nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
}

.alert-message {
  margin: 1rem auto 2rem;
  text-align: center;
  font-size: 2.4rem;
  color: yellow;
}
/* Header Component */

.header {
    margin-top: 0.9rem;
      text-align: center;
      font-family: 'Iceland', cursive;
    }
    
    .header > h1 {
      margin: 0;
      font-size: 7rem;
      font-weight: 500;
    }
/* Players Component */

.sub-heading {
    color: yellow;
    font-size: 1.6rem;
    font-style: italic;
    letter-spacing: 0.1rem;
    text-align: center;
    margin: 0;
  }

  .player-form {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .player-input {
    border: none;
    width: 16rem;
    margin: 0 1rem 0 0;
    padding-left: 1rem;
    text-align: left;
  }

  .player-rating-group {
    width: 12rem;
    margin: 0 1rem 0 0.6rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .player-rating-numbers {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
  }

  .player-rating-number {
    font-size: 0.8rem;
    width: 10%;
  }

  .main {
    max-width: 68%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
  
  .alert-box {
    text-align: center;
    margin: 0 0 1rem;
  }
  
  .btn-group {
    display: flex;
    justify-content: space-around;
    margin-bottom: 1.2rem;
  }
  
  .error-message {
    font-size: 1.5rem;
    text-align: center;
    padding: 0 2rem;
  }

  .settings-and-prev-btn {
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem;
  }

  .settings-and-prev-btn > a {
    margin: 0 auto;
  }

  @media only screen and (max-width: 1050px) {
    .main {
      flex-direction: column;
      align-items: center;
    }

    .player-form {
      flex-direction: column;
      align-items: center;
    }

    .sub-heading {
      margin-bottom: 0.5rem;
    }

    .label {
      margin-bottom: 0.5rem;
    }
  }
/* Player Component */

.players {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 25rem;
    padding: 0.2rem 1rem;
    background: white;
    color: black;
    box-shadow: 6px 6px 5px 0px rgba(0,0,0,0.75);
    border-radius: 6px;
    margin-bottom: 1.6rem;
  }
  
  .player {
    font-size: 1.8rem;
    margin: 0;
    color: rgb(37, 37, 37);
  }

  .player-rating {
    font-size: 1.4rem;
    margin: 0 0.3rem 0 0;
    color: rgb(37, 37, 37);
  }
  
  .player-options {
    display: flex;
    align-items: center;
  }
  
  .player-options > button {
    margin-left: 0.6rem;
    padding: 0.2rem 0.6rem;
    font-size: 1rem;
  }

  .update-player-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .update-player-form > input {
    margin-top: 0.3rem;
  }
  
  .update-player-btn {
    margin: 0.8rem auto;
    padding: 0.3rem;
    font-size: 1rem;
    width: 4rem;
    text-align: center;
  }

/* Settings Component */

.settings-form {
    align-self: flex-start;
    background-color:hsla(55, 65%, 89%, 0.7);
    border-radius: 6px;
    padding: 2.5rem 0 1.6rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 6px 6px 5px 0px rgba(0,0,0,0.9);
    margin-bottom: 2rem;
  }
  
  .team-heading {
    margin: 0;
    font-size: 2rem;
    color: black;
  }
  
  .names-container {
    max-width: 80%;
    min-width: 500px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
  }
  
  .team-name-input {
    width: 13rem;
  }
  
  .settings-label {
    text-align: center;
    color: black;
    margin: 0 0 0.7rem;
    font-size: 1.4rem;
  }
  
  .settings-label-names {
    margin: 0;
  }
  
  .settings-label-kits {
    margin-top: 1.2rem;
  }
  
  .kits-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    margin: 0 auto 1rem;
  }
  
  .colour-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1rem;
    margin-bottom: 1rem;
  }
  
  .colour {
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    cursor: pointer;
    border: 1px solid black;
  }
  
  .colour:nth-child(1) {
    background-color: navy;
  }
  
  .colour:nth-child(2) {
    background-color: red;
  }
  
  .colour:nth-child(3) {
    background-color: darkred;
  }
  
  .colour:nth-child(4) {
    background-color: green;
  }
  
  .colour:nth-child(5) {
    background-color: orange;
  }
  
  .colour:nth-child(6) {
    background-color: purple;
  }
  
  .colour:nth-child(7) {
    background-color: pink;
  }
  
  .colour:nth-child(8) {
    background-color: turquoise;
  }
  
  .colour:nth-child(9) {
    background-color: yellow;
  }
  
  .colour:nth-child(10) {
    background-color: darkolivegreen;
  }
  
  .colour:nth-child(11) {
    background-color: aquamarine;
  }
  
  .colour:nth-child(12) {
    background-color: indianred;
  }
  
  .football-shirt {
    height: 100px;
    width: 70px;
    position: relative;
    z-index: 3;
  }
  
  .left-sleeve {
    position: absolute;
    transform: translateX(-25px) translateY(3px) rotate(50deg);
    height: 50px;
    width: 30px;
  }
  
  .right-sleeve {
    position: absolute;
    transform: translateX(65px) translateY(3px)rotate(-50deg);
    height: 50px;
    width: 30px;
  }
/* Teams Component */

.teams-wrapper {
    max-width: 68%;
    min-width: 500px;
    margin: 1.6rem auto 0.5rem;
    display: flex;
    justify-content: space-between;
  }
  
  .team-group {
    background-color: rgba(0,0,0,0.5);
    box-shadow: 4px 4px 3px 0px rgba(0,0,0,0.9);
    border-radius: 6px;
    padding: 1rem 3rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    font-size: 1.2rem;
  }
  
  .team-name {
    font-size: 3rem;
    margin: 1rem 0;
    text-align: center;
  }
  
  .team-rating {
    color: yellow;
    font-style: italic;
    letter-spacing: 0.1rem;
  }
  
  .team-list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .team-player {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
  
  .btn-change-players {
    display: block;
    margin: 1.4rem auto 1.4rem;
  }
  
  .score-form {
    background-color:hsla(55, 65%, 89%, 0.7);
    box-shadow: 6px 6px 5px 0px rgba(0,0,0,0.9);
    border-radius: 6px;
    padding: 1rem 1rem 2rem;
    align-self: flex-start;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: black;
  }

  .score-team {
    margin: 0;
    font-size: 2rem;
  }

  .score-input {
    text-align: left;
    padding-left: 0.7rem;
    margin-bottom: 2rem;
    height: 2rem;
    width: 80%;
  }

  @media only screen and (max-width: 1000px) {
    .teams-wrapper {
      flex-direction: column;
    }

    .team-group {
      margin-bottom: 2rem;
    }

    .score-form {
      align-self: center;
      margin-bottom: 2rem;
      order: 3;
    }
  }
/* Loading component */

.loader {
    margin: 20rem auto 0;
    border: 16px solid #f3f3f3;
    border-top: 16px solid yellow; 
    border-radius: 50%;
    width: 120px;
    height: 120px;
    -webkit-animation: spin 2s linear infinite;
            animation: spin 2s linear infinite;
  }
  
  @-webkit-keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
/* History Component */

.history-table {
    margin: 2rem auto;
    background-color: rgba(0,0,0,0.5);
    box-shadow: 4px 4px 3px 0px rgba(0,0,0,0.9);
    padding: 1rem 1.8rem;
  }
  
  .table-head {
    color: yellow;
    font-size: 2.3rem;
  }
  
  .table-head > tr > th {
    padding: 0 2rem;
  }
  
  .table-row {
    text-align: center;
  }
  
  td {
    padding: 0.8rem 3rem;
    font-size: 1.9rem;
  }

  .clear-history-btn {
    margin: 0 auto 1.5rem;
    display: block;
  }

  .home-btn {
    display: block;
    margin: 0 auto;
  }

